.wrapperDialog {
  display: grid;
  grid-auto-columns: 1fr;
  grid-template-columns: 100%;
  grid-template-rows: 1fr;
  gap: 0px 0px;
  grid-template-areas: "wrapperContactsMessag";
  background-color: #8f0048ad;
}

.wrapperContactsDialogs {
  display: grid;
  width: 100%;
  height: 550px;
  grid-template-rows: 10% 85% 5%;
  gap: 0px 0px;
  grid-template-areas:
    "headerDialogs"
    "usersDialogs"
    "footerDialogs";
}

.wrapperContactsMessage {
  grid-area: wrapperContactsMessag;
  flex-direction: column;
  justify-content: space-between;
  height: 550px;
  background-color: rgb(126 10 237 / 26%);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 10% 64% 26%;
  grid-auto-columns: 1fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    "headerMessage"
    "userMessage"
    "footerMessage";
}

.headerDialogs {
  grid-area: headerDialogs;
  background-color: #1a70f1b3;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.headerDialogs span {
  height: 100%;
  font-size: 2.4rem;
  padding-left: 30px;
  font-weight: bold;
  color: #856af5;
  cursor: default;
}

.usersDialogs {
  height: 100%;
  grid-area: usersDialogs;
  background-color: #1d159b73;
  overflow-y: scroll;
}
.usersDialogs::-webkit-scrollbar {
  width: 12px;
}

.usersDialogs::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(248, 248, 248, 0.3);
}

.usersDialogs::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px rgba(182, 149, 248, 0.5);
}

.footerDialogs {
  height: 100%;
  grid-area: footerDialogs;
  background-color: rgb(36 61 217);
  width: 100%;
}

.headerMessage {
  grid-area: headerMessage;
  border-radius: 0px 0px 20px 20px;
  background-color: #ed08b14d;
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.headerMessage span {
  padding: 10px 0px 10px 0px;
}
.fullNameDialog {
  font-size: 1.4rem;
  font-weight: bold;
  color: #ffaae5;
  font-style: italic;
}
.statusChat {
  font-size: 0.9rem;
  color: #f77171;
  font-weight: 600;
}

.userMessage {
  grid-area: userMessage;
  overflow-y: scroll;
}
.usersDialogs::-webkit-scrollbar {
  width: 12px;
}

.userMessage::-webkit-scrollbar {
  width: 12px;
}

.userMessage::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(106, 36, 172, 0.3);
  border-radius: 10px;
}

.userMessage::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(37, 5, 219, 0.5);
}

.footerMessage {
  grid-area: footerMessage;
  border-radius: 20px 20px 0px 0px;
  background-color: #ed08b14d;
  width: 100%;
}

.showDialogs_btn {
  background-color: #960d89b0;
  width: 80px;
  height: 100%;
  padding: 0px 0px 0px 10px;
  border-bottom-left-radius: 40%;
  cursor: pointer;
  font-size: 2rem;
  font-weight: bold;
  letter-spacing: -10px;
  color: #556cff;
}

.wrapperSingleDialog {
  min-width: 300px;
  max-width: 99%;
  margin-left: 0.5%;
  height: 60px;
  background-color: #4ad9bc60;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  outline: #0076f5 solid 1px;
  box-shadow: 0px 0px 5px #43d1f9;
}
.activeLink > .wrapperSingleDialog {
  background-color: #7207ff29;
  box-shadow: 0px 0px 10px #3a00f7c2;
}
.wrapperSingleDialog:hover {
  outline: 1px solid rgba(17, 147, 180, 0.65);
  background-color: rgba(115, 73, 184, 0.55);
}
a {
  text-decoration: none;
}

.infoDialog {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
}
.infoName {
  font-size: 1.2rem;
  font-weight: 700;
  color: #9980ff;
}
.infoStatus {
  font-size: 1rem;
  padding-left: 5px;
  font-weight: 600;
  color: #ff6262;
}
.avatar {
  width: 60px;
  height: 60px;
  background-color: rgb(35, 167, 207);
  margin-right: 10px;
}
.avatar img {
  width: 60px;
  height: 60px;
}
.emptyDialogs {
  padding: 40px 0px 0px 30px;
  color: #bddaff;
}
.containerMessage {
  width: 96%;
  margin: 0px 1% 2% 2%;
  display: flex;
  flex-direction: column;
}
.singleMessage {
  background-color: #8e33f147;
  display: flex;
  flex-direction: column;
  width: 40%;
  padding: 5px;
  border-radius: 8px;
  margin: 10px 0px 5px 10px;
  word-break: break-word;
}
.messageOther {
  align-self: flex-end;
}
.messageBody {
  padding: 5px 15px;
}
.messageTime {
  align-self: end;
  font-size: 0.6rem;
}
/* Animation Begin */
.animationHideDialogs {
  transition: width 4.5s, opacity 1.4s, visibility 1s;
  visibility: hidden;
  width: 0%;
  opacity: 0;
}
.animationShowDialogs {
  transition: width cubic-bezier(0.15, 0.2, 0.15, 1) 2.3s,
    opacity cubic-bezier(0.8, 0.45, 0.87, 0.26) 0.8s;
  visibility: visible;
  width: 100%;
  opacity: 1;
}
.animWrapperActive {
  position: absolute;
  transition: 1s linear width;
  width: 35%;
}
.animWrapperDisabled {
  position: absolute;
  transition: 1.5s linear width;
  width: 0%;
}
.animHideBtn {
  transition: 1.2s linear visibility, 1.4s linear opacity, 1.6 linear width;
  visibility: hidden;
  opacity: 0;
}
.animShowBtn {
  transition: 2.2s linear visibility, 1.8s linear opacity, 1.6 linear width;
  opacity: 1;
  visibility: visible;
  height: 10%;
}
/* Animation End */

.untilSelectText {
  grid-area: userMessage;
  display: flex;
  justify-content: space-between;
}

.untilSelectText span {
  color: #6fafcf;
  font-size: 1.6rem;
  padding: 0px 0px 0px 25px;
  font-weight: 600;
  width: 50%;
  text-shadow: 2px 2px 8px #ffffffcf;
}
